.hero{
  background-color:var(--hero-bckgr-color);
  padding-bottom: 160px;
  padding-top: 6px;
@media screen and(min-width: 768px) {
      padding-top: 37px;
      padding-bottom: 28px;
  }
  @media screen and(min-width: 768px) {
    padding-bottom: 45px;
}
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  @media screen and(min-width: 768px) {
    justify-content: normal;
}
}
.header__mob-button{
  border: none;
  padding: 0;
  background-color: initial;
  color: var(--hover-social-icon-color);
  @media screen and(min-width: 768px) {
    margin-left: auto;
}
  @media screen and(min-width: 1200px) {
    display: none;
  }
}
.header__button{
  display: none;
  @media screen and(min-width: 768px) {
    display: block;
    margin-left: 30px;
    width: 160px;
    height: 44px;
    background-color: var(--second-btn-color);
    color: var(--main-btn-color);
    border-radius: 22px;
    font-size: 16px;
    line-height: 1.3;
    font-weight: 700;
    border: none;
    @media screen and(min-width: 1200px) {
      margin-left: 110px;
    }
}
}
.header__list{
  display: none;
  @media screen and(min-width: 1200px) {
    display: flex;
    margin-left: auto;
}
.header__item:not(:last-child){
  margin-right: 35px;
}
}
.header__link{
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  color: var(--main-text-color);
}
.hero-left{
  margin-bottom: 20px;
  margin-top: 137px;
  @media screen and(min-width: 768px) {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
  @media screen and(min-width: 1200px) {
    margin-top: 83px;
  }
}
.hero-rigth__item{
  position: relative;
  z-index: 1;
}
.hero-rigth__item:not(:last-child){
  margin-bottom: 14px;
  }
.hero-rigth__span{
  font-family: 'Titan One', cursive;
  font-size: 28px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: var(--second-text-color);
  @media screen and(min-width: 1200px) {
    font-size: 46px;
}
  
}
.hero-rigth__text{
  font-size: 8px;
  line-height: 1.3;
  color: var(--main-text-color);
  @media screen and(min-width: 1200px) {
    font-size: 14px;
}
}

.ellipse{
  background-color: #ffffff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  position: absolute;
  top: -10%;
  right: -10%;
  z-index: -1;
  @media screen and(min-width: 1200px) {
    width: 30px;
    height: 30px;
    top: -8px;
    right: -12px;
}
}
.hero-rigth__list{
  display: none;
  @media screen and(min-width: 768px) {
    display: block;
    margin-right: 8px;
}
}
.hero-rigth__item{
  text-align: end;
}


.hero-left__title{
  font-family: 'Titan One', cursive;
  text-transform: uppercase;
  color: var(--second-title-color);
  font-size: 26px;
  line-height: 1.14;
  letter-spacing: 0.04em;  
  width: 239px;  
  @media screen and(min-width: 768px) {
    font-size: 22px;
    line-height: 1.14;
    width: 200px;
}
  @media screen and(min-width: 768px) {
    font-size: 38px;
    line-height: 1.14;
    width: 277px;
  }
}
.hero__span{
  font-family: 'Titan One', cursive;
  text-transform: uppercase;
  color: var(--second-text-color);
  font-size: 48px;
  line-height: 0.9;
  @media screen and(min-width: 768px) {
    font-size: 34px;
    line-height: 0.88;
    letter-spacing: 0.04em;
  }
  @media screen and(min-width: 768px) {
    font-size: 58px;
  }
}
.hero-down{
  display: none;
  @media screen and(min-width: 768px) {
    display: flex;
    align-items: end;
  }
}
.hero-down__label{
  margin-left: 30px;
}
.hero-down__text{
  font-size: 8px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--main-text-color);
  width: 114px;
  @media screen and(min-width: 1200px) {
    font-size: 14px;
    line-height: 1.4;
    width: 200px;
  }
}
.hero-donw__btn{
  border: none;
  border-radius: 50%;
  background-color: var(--reviews-bckgr-color);
  color: var(--main-btn-color);
  margin-top: 7px;
  @media screen and(min-width: 1200px) {
    margin-top: 20px;
    width: 26px;
    height: 26px;
  }
}
.hero-down__icon{
  fill: currentColor;
}
.hero-btn{
  display: flex;
  margin-bottom: 19px;
  @media screen and(min-width: 1200px) {
    margin-top: 56px;
  }
}
.hero-btn__left{
  cursor: pointer;
  padding: 0;
  width: 63px;
  height: 24px;
  background-color: #D41443;
  border-radius: 20px;
  border: none;
  font-size: 8px;
  line-height: 1.3;
  font-weight: 400;
  color: var(--second-btn-color);
  margin-right: 8px;
    @media screen and(min-width: 768px) {
      width: 102px;
      height: 40px;
      font-size: 14px;
    }
}
.hero-btn__rigth{
  cursor: pointer;
  padding: 0;
  width: 83px;
  height: 24px;
  border: none;
  background-color: #ffffff;
  border-radius: 20px;
  font-size: 8px;
  line-height: 1.3;
  font-weight: 400;
  color: var(--main-btn-color);
  @media screen and(min-width: 768px) {
    width: 138px;
    height: 40px;
    font-size: 14px;
  }
}
.mob-menu{
  position: fixed;
  background-color: var(--mobile-menu-color) ;
  width: 250px;
  height: 100vh;
  top:0;
  right: 0;
  padding-left: 21px;
  padding-right: 21px;
  padding-top: 60px;
  transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);
        
}
.mob-menu__btn-close{
  border: none;
  padding: 0;
  background-color: initial;
  position: absolute;
  top: 23px;
  right: 28px;
}
.mob-menu__btn{
  width: 209px;
  height: 44px;
  border: none;
  padding: 0;
  background-color: var(--second-btn-color);
  color: var(--main-btn-color);
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 20px;
}
.mob-menu__item{
  border-top: 1px solid #E18298;
  border-bottom: 1px solid #E18298;
  padding-top: 13px;
  padding-bottom: 11px;
}
.mob-menu__link{
  font-size: 14px;
  font-weight: 700;
  line-height: 1.3;
  color: var(--main-text-color);
  
}
.is-hidden{
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}